@use "." as this;
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Element/Link";
@use "@app/bancor-components/Element/Image";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Border";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Padding";
@use "@app/bancor-components/Style/Layout/Position";
@use "@app/bancor-components/Style/Layout/Display";

#{this.$selector} {
    @include Padding.set($preset: gamma);

    .logo150 {
        @include Position.set($position: absolute);
        width: calc(Layout.get-size(beta) + Layout.get-size(delta));
        height: Layout.get-size(iota);
        right: Layout.get-size(alpha);
        top: Layout.get-size(none);
    }

    // Component Button
    #{Button.$selector} {
        min-width: calc(Layout.get-space(alpha) + Layout.get-space(delta));
        min-height: calc(Layout.get-space(alpha) + Layout.get-space(delta));
    }

    // Component Link
    #{Link.$selector} {
        @include Flex.set($preset: null, $align: center);
        @include Color.set($fg: "gray-dark");
        @include Font.set($deco: default);
        div {
            transition: background-color 0.8s, box-shadow 0.3s ease;
        }
        &:focus > div {
            @include Color.set($bg: "primary");
            svg path {
                @include Color.set($svg-fg: "white");
            }
        }
    }
    // Component Button with Icon and Text
    .chatModal {
        #{Button.$selector} {
            @include Padding.set(none);
            @include Flex.set($align: center);
            @include Color.set($bg: "transparent");
            @include Border.set($style: default);
            cursor: pointer;
            div {
                @include Margin.set($right: alpha);
                @include Flex.set($align: center, $justify: center);
                @include Border.set($radius: kappa, $width: beta, $style: solid, $color: "primary");
                transition: background-color 0.8s, box-shadow 0.3s ease;
                width: calc(Layout.get-space(alpha) + Layout.get-space(delta));
                height: calc(Layout.get-space(alpha) + Layout.get-space(delta));
            }
            svg {
                @include Border.set($style: none);
            }
            &:focus > div {
                @include Color.set($bg: "primary");
                svg path {
                    @include Color.set($svg-fg: "white");
                }
            }
        }
    }

    & > header {
        // Component Text
        #{Text.$selector} {
            @include Margin.set($bottom: delta);
            @include Color.set($fg: "primary");
            @include Font.set($size: delta, $transform: upper, $weight: bold);
        }
    }

    & > footer {
        > section {
            @include Flex.set($align: center, $justify: center, $dir: column);
            @include Margin.set($bottom: delta);

            #{Text.$selector}:first-child {
                @include Font.set($size: gamma, $weight: normal, $family: "default");
                @include Color.set($fg: "gray-dark");
            }

            #{Link.$selector} {
                @include Flex.set($align: center, $justify: center);
                @include Border.set($radius: iota, $width: alpha, $style: solid, $color: "primary");
                @include Margin.set($top: alpha, $bottom: alpha);
                width: Layout.get-max-width(alpha);

                #{Text.$selector} {
                    @include Font.set($size: gamma, $weight: medium, $family: "default");
                    @include Color.set($fg: "primary");
                }
            }

            // Component Text Last Connection
            #{Text.$selector} {
                @include Padding.set($top: alpha, $bottom: alpha);
                @include Color.set($fg: "gray-light");
                @include Font.set($size: beta, $weight: normal, $family: "default");
            }
        }

        .version {
            @include Flex.set($preset: null, $justify: end);
            @include Font.set($size: alpha, $weight: normal, $family: "default");
        }
    }

    #{this.$selector-list},
    #{this.$selector-nav} {
        & > ul {
            & > li {
                @include Padding.set($top: alpha, $bottom: alpha, $left: alpha);
                #{Link.$selector} {
                    #{Text.$selector} {
                        @include Color.set($fg: "gray-dark");
                        @include Font.set($size: gamma, $weight: normal, $family: "default");
                    }
                }
                &.active {
                    @include Border.set($radius: iota);
                    @include Color.set($bg: "primary-variant-100");
                    #{Link.$selector} {
                        #{Text.$selector} {
                            @include Color.set($fg: primary);
                        }
                    }
                }
            }
        }
    }
}

#{this.$selector-list} {
    & > h3 {
        @include Margin.set($bottom: alpha);
        @include Font.set($size: delta, $weight: bold, $family: "heading");
    }
}

// Customer Support
#{this.$selector-grid} {
    & > h3 {
        @include Margin.set($bottom: alpha);
        @include Font.set($size: delta, $weight: bold, $family: "heading");
    }
    .retailCustomerSupport {
        @include Display.set($display: grid);
        grid-template-rows: 1fr 1fr;
        grid-template-columns: 140px 140px;
    }
    .corporateCustomerSupport {
        @include Display.set($display: inline-grid);
    }
    & > ul {
        & > li {
            @include Padding.set($top: alpha);
            @include Flex.set($align: center);

            #{Link.$selector} {
                #{Text.$selector} {
                    @include Color.set($fg: "gray-dark");
                    @include Font.set($size: gamma, $weight: normal, $family: "default");
                    @include Margin.set($left: alpha);
                }
                div {
                    @include Flex.set($align: center, $justify: center);
                    @include Border.set(
                        $radius: kappa,
                        $width: beta,
                        $style: solid,
                        $color: "primary"
                    );
                    @include Padding.set($preset: beta);
                    width: calc(Layout.get-space(alpha) + Layout.get-space(delta));
                    height: calc(Layout.get-space(alpha) + Layout.get-space(delta));
                    > svg {
                        @include Flex.set($shrink: none);
                    }
                }
            }
        }
    }
}
